import React, { useState, useEffect } from 'react';
import axios from 'axios';








async function subscribeToChannel({ setMessage }:any) {
    

    const UPSTASH_URL = `https://rare-coral-35556.upstash.io`
    const OPERATION = `subscribe`
    const CHANNEL = `test-channel`
    const ACCESS_TOKEN = `xxxxxxxxx`

    try {
        const response = await fetch(
            `${UPSTASH_URL}/${OPERATION}/${CHANNEL}`,
            {
                method: "POST",  // ✅ Upstash requires POST for subscribe
                headers: {
                    "Authorization": `Bearer ${ACCESS_TOKEN}`,
                    "Accept": "text/event-stream"  // ✅ Required for streaming
                }
            }
        );

        if (!response.ok) throw new Error(`Subscription failed: ${response.statusText}`);

        const reader = response.body?.getReader();
        if (!reader) throw new Error("Failed to get response reader");

        const decoder = new TextDecoder();
        let partialChunk = "";

        while (true) {
            const { value, done } = await reader.read();
            if (done) break;

            partialChunk += decoder.decode(value, { stream: true });

            const lines = partialChunk.split("\n");
            partialChunk = lines.pop() ?? ""; // Save incomplete chunk for next read

            for (const line of lines) {
                if (line.startsWith("data: ")) {
                    console.log("Received message:", line.slice(6).trim());
                }
            }
        }
        console.log("Connection closed, reconnecting...");
        setTimeout(subscribeToChannel, 5000);
    } catch (error) {
        console.error("Subscription error:", error);
        setTimeout(subscribeToChannel, 5000);
    }
}




const IndexPage = () => {
    const VERSION : any = 4
    const [logs, setLogs] = useState([]);
    const CHANNEL = "test-channel";

    useEffect(() => {
        subscribeToChannel(setLogs);
    }, []);
    

    return (
        <div>
            <h1>Upstash Pub/Sub Test {VERSION}</h1>
            <p>Waiting for messages from Upstash...</p>
            <ul>
                {logs.map((msg, index) => (
                    <li key={index}>{msg}</li>
                ))}
            </ul>
        </div>
    );
};




